// CommonJS   
// let $ = require('jquery');

// ECMAScript 2015: ES 6th Edition   
import $ from 'jquery';

//   init()  
$(document).ready(init);

//  
function init() {

  //   <nav> 
  let $nav = $('nav');
  // <nav>  <a>  
  let $nav_links = $nav.find('a');
  // <nav>  <a>    ()
  $nav_links.each((idx, item) => {
    // $link    $(<a>) 
    let $link = $nav_links.eq(idx);
    // $link     
    $link.on('click', (e) => {
      //     
      e.preventDefault();
      // idx   
      console.log(idx);
    });
  });

};






